import React,{Component} from 'react';
import Loading from '../loading/lottie';

export default (WrappedComponent)=>{
    return class newRender extends Component{
        constructor(){
            super(...arguments);
            this.state = {
                loading:false,
                url:this.props.url,
                listData:null
            };
        }
        componentDidMount(){
            const getAjax = new XMLHttpRequest();
            console.log('/'+this.state.url)
            getAjax.open('GET','/'+this.state.url);
            getAjax.onload = ()=>{
                if (getAjax.readyState === 4 && getAjax.status === 200) {
                    this.setState({
                        loading:true,
                        listData:JSON.parse(getAjax.response).data
                    })
                }
            };
            getAjax.send();
        }
        render(){
            return this.state.loading?<WrappedComponent {...this.state.listData}/>:<Loading stop={this.state.loading}/>
        }
    }
}




