import React from "react";
import styles from "./index.module.css";
import List from "./list/cateLiist";

export default ({ wapBannerUrl, name, subCateList }) => (
  <div className={styles.subCateList}>
    <img src={wapBannerUrl} alt="" className={styles.banner} />
    <div className={styles.title}>
      {name}
      
    </div>
    <List subCateList={subCateList} />
  </div>
);



