import React, { Component } from "react";
import WrappedComponent from "animationLoad/wrapper";
import Header from "./header/header";
import { Tabs } from "antd-mobile";
import List from "./body/index";
import "antd-mobile/lib/tabs/style/index.css";
import styles from "./index.module.css";

class CateList extends Component {
  constructor() {
    super(...arguments);
    const listData = Array.from(
      Object.keys(this.props),
      index => this.props[index]
    );
    const categoryList = Array.from(listData, ({ name }) => ({
      title: name
    }));
    this.state = {
      cateNav: categoryList,
      cateList: listData
    };
  }
  render() {
    return (
      <div style={{ height: window.innerHeight }}>
        <Header />
        <div className={styles.wrapper}>
          <Tabs
            tabs={this.state.cateNav}
            usePaged={false}
            initialPage={3}
            tabBarPosition="left"
            tabDirection="vertical"
            renderTabBar={props => (
              <Tabs.DefaultTabBar {...props} page={this.state.cateNav.length} />
            )}
            tabBarUnderlineStyle={"#ab2b2b"}
            tabBarActiveTextColor={"#ab2b2b"}
          >
            {this.state.cateList.map(({ subCateList, id, wapBannerUrl }) => (
              <React.Fragment key={id}>
                <List subCateList={subCateList} wapBannerUrl={wapBannerUrl} />
              </React.Fragment>
            ))}
          </Tabs>
        </div>
      </div>
    );
  }
}

export default WrappedComponent(CateList);



